  font-size: 55px;
  line-height: 1;
}

#masthead .well {
  margin-top:13%;
    border-color:#000033;
}

.icon-bar {
  background-color:#fff;
}


.navbar-bright {
  background-color:#111155;
    color:#fff;
}
  
